import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import axios from "axios";

export const getProduct = createAsyncThunk( 'product/getProduct', async () => {
  
    try {
      const response = await axios.get("https://625bf62850128c570209bacc.mockapi.io/product")
      return response.data

    } catch (err) {
      console.log(err)
    }
 })
export const addProduct = createAsyncThunk("product/add", async (item) => {
  const res = await axios.post("https://625bf62850128c570209bacc.mockapi.io/product", item)
  return res.data;
})

export const updateProduct = createAsyncThunk("product/update", async (update) => {
  const res = await axios.put(`https://625bf62850128c570209bacc.mockapi.io/product${update.id}`, update.model)
  return res.data
})

export const deleteProduct = createAsyncThunk(
  "product/delete",
  async (id) => {
    const res = await axios.delete(`https://625bf62850128c570209bacc.mockapi.io/product${id}`);
    return res.data;
  }
);
export const rejectProduct = createAsyncThunk("product/reject", async (id) => {
  const res = await axios.patch(`https://625bf62850128c570209bacc.mockapi.io/product${id}`);
  return res.data
})

const productSlice = createSlice({
  name: 'products',
  initialState: {
    product: [],
    isFetching: false,
    error: false,
  },

  extraReducers: (builder) => {

    builder.addCase(getProduct.fulfilled, (state, action) => {
      state.isFetching = false;
      state.product = action.payload;
    });
    builder.addCase(addProduct.fulfilled, (state, action) => {
      state.productDetail = action.payload;
    })
    builder.addCase(updateProduct.fulfilled, (state, action) => {
      state.isFetching = false;
      state.productDetail = action.payload;
    })
    builder.addCase(deleteProduct.fulfilled, (state, action) => {
      return state.products.filter((product) => product.id !== action.payload.id)
     });
    builder.addCase(rejectProduct.fulfilled, (state, action) => { });
  }
})

export const {
  productReducer
} = productSlice.actions;

export default productSlice.reducer;