import './Admin.scss';
import NavbarAdmin from './NavbarAdmin';
import DeleteIcon from '@mui/icons-material/Delete';
import EditIcon from '@mui/icons-material/Edit';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import Backdrop from '@mui/material/Backdrop';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Modal from '@mui/material/Modal';
import Fade from '@mui/material/Fade';
import IconButton from '@mui/material/IconButton';
import Stack from '@mui/material/Stack';
import TextField from '@mui/material/TextField';
import FormControl from '@mui/material/FormControl';
import Footer from '../../scss/layout/Footer';
import { getUser, addUser, updateUser, deleteUser } from '../../redux/userSlice';
import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';

const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 400,
  bgcolor: 'background.paper',
  border: '1px solid #dbe3d2',
  opacity: 0.4,
  boxShadow: 4,
  padding: 4,
};

function Admin() {
  const users = useSelector((state) => {
    console.log('dsfsdfds', state.user)
    return state.user.user;
  });
  const [open, setOpen] = useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(getUser());
    console.log('text', users)

  }, []);

  const handleSubmit = (event) => {
    event.preventDefault();
    console.log(event.target.value);
  };

  const handleAddUser = () => {
    dispatch(addUser());
    console.log(addUser());
  };

  const handleDeleteUser = (id) => {
    dispatch(deleteUser(id));
  };

  const handleUpdateUser = (id) => {
    dispatch(updateUser(id));
  };
  return (
    <>
      <NavbarAdmin />
      <div className="dashboard">
        <TableContainer component={Paper}>
          <Table aria-label="simple table">
            <TableHead>
              <TableRow>
                <TableCell>STT</TableCell>
                <TableCell>User Name</TableCell>
                <TableCell>Email</TableCell>
                <TableCell>Birthday</TableCell>
                <TableCell>Action</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {              
              users.map((user, id) => (
                <TableRow sx={{ '&:last-child td, &:last-child th': { border: 0 } }} key={id}>
                  <TableCell component="th" scope="row">
                    {user.id}
                  </TableCell>
                  <TableCell>{user.name}</TableCell>
                  <TableCell>{user.email}</TableCell>
                  <TableCell>{user.birthday}</TableCell>
                  <TableCell>
                    <IconButton onClick={handleOpen}>
                      <EditIcon className="icon" />
                    </IconButton>
                    <Modal
                      aria-labelledby="transition-modal-title"
                      aria-describedby="transition-modal-description"
                      open={open}
                      onClose={handleClose}
                      closeAfterTransition
                      BackdropComponent={Backdrop}
                      BackdropProps={{
                        timeout: 500,
                      }}
                    >
                      <Fade in={open}>
                        <Box sx={style}>
                          <FormControl variant="standard" sx={{ m: 1 }}>
                            <TextField
                              required
                              label="User Name"
                              variant="standard"
                              fullWidth={true}
                              className="editUser"
                              autoFocus={true}
                            />
                            <TextField
                              required
                              label="Email"
                              variant="standard"
                              fullWidth={true}
                              className="editUser"
                            />
                            <TextField
                              required
                              label="Birthday"
                              variant="standard"
                              fullWidth={true}
                              className="editUser"
                            />
                          <Button >Cancel</Button>
                          <Button
                            onClick={() => {
                              handleUpdateUser(user.id);
                            }}
                          >
                            Update
                          </Button>
                          </FormControl>
                        </Box>
                      </Fade>
                    </Modal>
                    &nbsp;
                    <IconButton
                      onClick={() => {
                        handleDeleteUser(user.id);
                      }}
                    >
                      <DeleteIcon className="icon" />
                    </IconButton>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
        <Stack direction="row" spacing={2} className="addUser">
          <FormControl variant="standard" sx={{ m: 1, minWidth: 120 }}>
            <IconButton onClick={handleAddUser}>Add User</IconButton>
            <TextField label="User Name" variant="standard" className="input--edituser" />
            <TextField label="Email" variant="standard" className="input--edituser" />
            <TextField label="Birthday" variant="standard" className="input--edituser" />
          </FormControl>
        </Stack>
      </div>
      <Footer />
    </>
  );
}

export default Admin;
