import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import axios from "axios";


export const getCartItems = createAsyncThunk('cart/getCartItems', async () => {
    try {

        const res = await axios.get("https://625bf62850128c570209bacc.mockapi.io/cart");
        return res.data;
    } catch (err) {
        console.log(err)
    }
})

export const addCartItems = createAsyncThunk('cart/getCartItems', async () => {
    try {

        const res = await axios.post("https://625bf62850128c570209bacc.mockapi.io/cart");
        return res.data;
    } catch (err) {
        console.log(err)
    }
})



const cartSlice = createSlice({
    name: 'cart',
    initialState: {
        cart: [],
        quantity: 0,
        total: 0,
        isFetching: false,
    },
    reducers: {
        clearCart: (state) => {
            state.cart = [];
          },
          removeItem: (state, action) => {
            const itemId = action.payload;
            state.cartItems = state.cartItems.filter((item) => item.id !== itemId);
          },
          quantity: (state, { payload }) => {
            const cartItem = state.cartItems.find((item) => item.id === payload.id);
            cartItem.amount = cartItem.amount + 1;
          },
          decreasequantity: (state, { payload }) => {
            const cartItem = state.cartItems.find((item) => item.id === payload.id);
            cartItem.amount = cartItem.amount - 1;
          },
          calculateTotal: (state) => {
            let amount = 0;
            let total = 0;
            state.cartItems.forEach((item) => {
              amount += item.amount;
              total += item.amount * item.price;
            });
            state.amount = amount;
            state.total = total;
          },
    },
    extraReducers: (builder) => {
        // builder.addCase()
    }
})

export const { addProduct, removeProduct, increaseQuantity, decreaseQuantity } = cartSlice.actions;
export default cartSlice.reducer;