import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import axios from "axios";

export const getUser = createAsyncThunk('user/getUser', async () => {

  try {
    console.log("1")
    const response = await axios.get("https://625bf62850128c570209bacc.mockapi.io/user")
    console.log("2")

    return response.data

  } catch (err) {
    console.log(err)
  }
})

export const addUser = createAsyncThunk('user/add', async () => {

  try {
    const response = await axios.post("https://625bf62850128c570209bacc.mockapi.io/user")
    return response.data

  } catch (err) {
    console.log(err)
  }
})
export const updateUser = createAsyncThunk('user/update', async (id) => {

  try {
    const response = await axios.put(`https://625bf62850128c570209bacc.mockapi.io/user/${id}`)
    return response.data

  } catch (err) {
    console.log(err)
  }
})
export const deleteUser = createAsyncThunk('user/delete', async (id) => {
  console.log(id)
  try {
    const response = await axios.delete(`https://625bf62850128c570209bacc.mockapi.io/user/${id}`)
    return response.data
  } catch (err) {
    console.log(err)
  }
})


const userSlice = createSlice({
  name: "user",
  initialState: {
    user: [],
    isFetching: false,
    error: false,
  },

  extraReducers: (builder) => {

    builder.addCase(getUser.fulfilled, (state, action) => {
      state.isFetching = false;
      state.user = action.payload;
    });
    builder.addCase(addUser.fulfilled, (state, action) => {
      state.productDetail = action.payload;
    })
    builder.addCase(updateUser.fulfilled, (state, action) => {
      state.productDetail = action.payload;
    })
    builder.addCase(deleteUser.fulfilled, (state, action) => {
      
    });
  }
});

export const {
  userReducer
} = userSlice.actions;
export default userSlice.reducer;